@import '../../../styles/_colors';

.alternative {
  margin: 5px;

  &-selected {
    border: 1.5px;
    border-style: solid;
    border-color: $colorSelected;
    border-radius: 7px;
    box-shadow: 0px 0px 5px $colorSelected;
    -moz-box-shadow: 0px 0px 5px $colorSelected;
    -webkit-box-shadow: 0px 0px 5px $colorSelected;
  }

  &-selected-related {
    border: 1.5px;
    border-style: solid;
    border-color: $colorSelectedRelated;
    border-radius: 7px;
    box-shadow: 0px 0px 5px $colorSelectedRelated;
    -moz-box-shadow: 0px 0px 5px $colorSelectedRelated;
    -webkit-box-shadow: 0px 0px 5px $colorSelectedRelated;
  }

  &-hovered {
    border: 1.5px;
    border-style: solid;
    border-color: $colorHovered;
    border-radius: 7px;
    box-shadow: 0px 0px 5px $colorHovered;
    -moz-box-shadow: 0px 0px 5px $colorHovered;
    -webkit-box-shadow: 0px 0px 5px $colorHovered;
  }

  &-hovered-related {
    border: 1.5px;
    border-style: solid;
    border-color: $colorHoveredRelated;
    border-radius: 7px;
    box-shadow: 0px 0px 5px $colorHoveredRelated;
    -moz-box-shadow: 0px 0px 5px $colorHoveredRelated;
    -webkit-box-shadow: 0px 0px 5px $colorHoveredRelated;
  }

  &-hovered-selected {
    border: 1.5px;
    border-style: solid;
    border-color:  $colorHoveredSelected;
    border-radius: 7px;
    box-shadow: 0px 0px 5px  $colorHoveredSelected;
    -moz-box-shadow: 0px 0px 5px  $colorHoveredSelected;
    -webkit-box-shadow: 0px 0px 5px  $colorHoveredSelected;
  }
}

.alternative-image {
  padding: 3.5%;
  border-radius: 10px;
}

.alternative-toggle1 {
  position: absolute;
  z-index: 10;
  margin-top: 3.5%;
  margin-left: 3.5%;
}

.alternative-toggle2 {
  position: absolute;
  z-index: 10;
  margin-top: 3.5%;
  margin-left: 87.5%;
}