.navigationItem {
    box-sizing: border-box;
    display: block;
}


.navigationItem a {
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    color: #fff;
    font-family: 'Poppins';
    font-size: 1.3vw;
    font-weight: bold;
}

.name {
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    color: #fff;
    font-family: 'Poppins';
    font-size: 1.3vw;
    font-weight: bold;
}


.navigationItem a:hover,
.navigationItem a.active
{
    color: #40a4cb;
}


