.mainsContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 100%;
    height: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: row;
}

.controlsContainer {
    width: 10%;
    min-width: 122px;
    height: 100%;
}

.controlsContent {
    width: 100%;
    height: 100%;
    margin-top: 25px;
    flex:1;
}

.initials {
    font-size: 20px;
    color: white;
    font-weight: bold;
}

.userContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    
}

.userName {
    font-size: 1.3vw;    
    font-family: "Poppins";
    font-weight: bold;
}


.circle {
    width:30px;
    height: 30px;
    background-color:  #176287;
    margin-right: 5px;
    border-radius: 50%;
    margin-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #000;
}


.projectsContainer {
    width: 90%;
    display: flex;
    flex-direction: column;
    height: 100%;
    flex-wrap: wrap;
    overflow-y: scroll;
    border-left: 3px solid gray;
    
}


.projectsRow {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.addProjectContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.addProjectContainer:hover {
    background-color: #daebf7;
    cursor: pointer; 
}

.plusIcon {

    width: 20px;
    height: 20px;
    background-color: gray;
    margin-right: 5px;
}

.logoutContainer {
    margin-top: 3px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.logoutContainer:hover {
    background-color: #daebf7;
    cursor: pointer; 
}

.logoutText {
    font-size: 1.3vw;
    font-family: "Poppins";    
}

.addProjectText {
    font-size: 1.3vw;
    font-family: "Poppins";
}

.homeContainer {
    height: 6%;
    min-height: 45px;
    margin-top: 3px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.homeText {
    font-size: 1.3vw;
    font-family: "Poppins";
    margin-top: 26px;
    margin-left: 2px;
}

.homeContainer:hover {
    background-color: #daebf7;
    cursor: pointer; 
}

