
html, body {
    height: 100%;
}





.mainContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 100%;
    height: 100%;
    min-height: 100%;
    background-color: #2F303A;
    display: flex;
    flex-direction: column;
}

.content {
    height: 100%;
     flex-wrap: wrap;
    display: flex;
    flex-direction: row;
}

.imageContainer {
    display: flex;
    flex: 1;
    align-items: center;
   
    justify-content: center;
}

.titlesContainer {
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: column;
}



.titles {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 25%;
}




