@import '../../../styles/colors';

.button-div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 43px;
}

.button {
    padding: 0.7% 3.1% 0.7% 3.1%;
    border-radius: 20px;
}

.button-visible {
    padding: 0.4% 1.1% 0.4% 1.1%;
    border-radius: 50%;
}

.button-hidden {
    display: none;
}

.button:hover {
    background-color: $colorCurrentCommand;
    cursor: pointer;
}

.button-visible:hover {
    border: 4px solid;
    border-color: $colorCurrentCommand;
    cursor: pointer;
}