$colorBlack: #000;
$colorWhite: #FFF;
$colorScrollBarTrack: #CCCCCC;
$colorScrollBarThumb:#777777; 
$colorBoxShadow: hsla(0,0%,0%,0.2);
$colorGreyScale1:  hsla(0,0%,80%,1.0);
$colorUnselected: hsla(0, 0%, 57%, 0.4);    
$colorHovered: hsla(0,85%,43%,1.0);    
$colorSelected: hsla(122,37%,52%,1.0);
$colorSelectedRelated: hsla(62,100%,41%,1.0);
$colorHoveredRelated: hsla(353,100%,75%,1.0);    
$colorHoveredSelected: hsla(0,100%,50%,1.0);
$colorHoveredSelectedRelated: hsla(0,100%,50%,1.0);    
$colorSketch: hsla(0,100%,50%,1.0);
$colorSketchPath: hsla(0,100%,50%,1.0);
$colorBrush:  hsla(201,94%,46%,1.0);
$colorBrushed:  hsla(122,90%,52%,0.6);
$colorBrushSelected: hsla(120,73%,75%,1.0);    
$colorQuerySelected: hsla(335, 87%, 54%, 0.3);    
$colorQueryNotSelected:  hsla(198, 100%, 50%, 0.3);    
$colorSelectedCoordinate: hsla(340, 71%, 96%, 1.0);
$colorCurrentCommand: orange;
$colorNotificationBorder: hsla(0,100%,30%,0.7);
$colorNotificationSuccessBackground: hsl(102, 96%, 81%);
$colorNotificationSuccessGradient1: hsl(102, 100%, 54%);
$colorNotificationSuccessGradient2: hsl(126, 100%, 36%);
$colorNotificationWarningBackground: hsl(52, 89%, 82%); 
$colorNotificationWarningGradient1: hsl(52, 100%, 50%);
$colorNotificationWarningGradient2: hsl(50, 100%, 50%);
$colorNotificationErrorBackground: hsl(0, 100%, 77%);
$colorNotificationErrorGradient1: hsl(0, 100%, 43%);
$colorNotificationErrorGradient2: hsl(0, 100%, 30%);

$opacity0: 0.0;
$opacityMid1: 0.33;
$opacityMid2: 0.67;
$opacity1: 1.0;



:export {
    colorBlack: $colorBlack;    
    colorWhite: $colorWhite;
    colorScrollBarTrack: $colorScrollBarTrack;
    colorScrollBarThumb: $colorScrollBarThumb;
    colorBoxShadow: $colorBoxShadow;
    colorGreyScale1: $colorGreyScale1;
    colorUnselected: $colorUnselected;
    colorHovered: $colorHovered;
    colorSelected: $colorSelected;
    colorSelectedRelated: $colorSelectedRelated;
    colorHoveredRelated: $colorHoveredRelated;
    colorHoveredSelectedRelated: $colorHoveredSelectedRelated;
    colorHoveredSelected: $colorHoveredSelected;
    colorSketch: $colorSketch;
    colorSketchPath: $colorSketchPath;
    colorBrush: $colorBrush;
    colorBrushed: $colorBrushed;
    colorBrushSelected: $colorBrushSelected;
    colorQuerySelected: $colorQuerySelected;
    colorQueryNotSelected: $colorQueryNotSelected;
    colorSelectedCoordinate: $colorSelectedCoordinate;
    colorCurrentCommand: $colorCurrentCommand;
    colorNotificationBorder: $colorNotificationBorder;
    colorNotificationSuccessBackground: $colorNotificationSuccessBackground;
    colorNotificationSuccessGradient1: $colorNotificationSuccessGradient1;
    colorNotificationSuccessGradient2: $colorNotificationSuccessGradient2;
    colorNotificationWarningBackground: $colorNotificationWarningBackground;
    colorNotificationWarningGradient1: $colorNotificationWarningGradient1;
    colorNotificationWarningGradient2: $colorNotificationWarningGradient2;
    colorNotificationErrorBackground: $colorNotificationErrorBackground;
    colorNotificationErrorGradient1: $colorNotificationErrorGradient1;
    colorNotificationErrorGradient2: $colorNotificationErrorGradient2;
    opacity0: $opacity0;
    opacityMid1: $opacityMid1;
    opacityMid2: $opacityMid2;
    opacity1: $opacity1;
}







