.navigation {
    height: 12%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
}

.logo {
    height: 80%;
    display: flex;
    flex: 1;
}

.nav {
    height: 100%;
    flex: 1;
    display: flex;
    justify-content: flex-end;
}

.logoContainer {
    width: 10%;
    height: 100%;
    cursor: pointer;
}

.logoStyle {
    width: 88%;
}

