@import '../../styles/colors';

.button-div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45px;
}

.button {
    padding: 10px;
    border-radius: 20px;
}

.button:hover {
    background-color: $colorCurrentCommand;
    cursor: pointer;
}

.selectStyle {
    box-shadow: 2px 2px 14px 2px $colorGreyScale1;
    background-color: $colorWhite;
    border: 2px;
    border-radius: 5px;
    padding: 14px;
    box-sizing: border-box;
    height: 100%;
    z-index: 5;
    cursor: pointer;
}

[tabindex] {
    outline: none !important;
}

.inputBox {
    display: none;
}
 
