@import '../../styles/colors';

.right-click-menu {
  position: absolute;
  cursor: default;
  opacity: 0;
  width: 0;
  height: 0;
}

.right-click-menu ~ img {
  height: 50px;
  width: 50px;
  cursor: pointer;
  transition: .2s;
  transition-delay: 0.1s;
  transform: translateY(10px);
  box-shadow: 0px 0px 52px 32px  $colorWhite;
  box-sizing: content-box;
  // border: 8px;
  border-radius: 36px;
}

.right-click-menu ~ ul .right-click-icon:nth-child(1) {
  -ms-transform: rotate(0deg) translateX(60px); /* IE 9 */
  -webkit-transform: rotate(0deg) translateX(60px); /* Safari and Chrome */
  transform: rotate(0deg) translateX(60px);
}
.right-click-menu ~ ul .right-click-icon:nth-child(2) {
  -ms-transform: rotate(60deg) translateX(60px);
  -webkit-transform: rotate(60deg) translateX(60px);
  transform: rotate(60deg) translateX(60px);
}
.right-click-menu ~ ul .right-click-icon:nth-child(3) {
  -ms-transform: rotate(120deg) translateX(60px);
  -webkit-transform: rotate(120deg) translateX(60px);
  transform: rotate(120deg) translateX(60px);
}
.right-click-menu ~ ul .right-click-icon:nth-child(4) {
  -ms-transform: rotate(180deg) translateX(60px);
  -webkit-transform: rotate(180deg) translateX(60px);
  transform: rotate(180deg) translateX(60px);
}
.right-click-menu ~ ul .right-click-icon:nth-child(5) {
  -ms-transform: rotate(240deg) translateX(60px);
  -webkit-transform: rotate(240deg) translateX(60px);
  transform: rotate(240deg) translateX(60px);
}
.right-click-menu ~ ul .right-click-icon:nth-child(6) {
  -ms-transform: rotate(300deg) translateX(60px);
  -webkit-transform: rotate(300deg) translateX(60px);
  transform: rotate(300deg) translateX(60px);
}

.right-click-icon {
  cursor: pointer;
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 40px;
  height: 40px;
}

.right-click-icon a {
  display: block;
  width: inherit;
  height: inherit;
  font-size: 20px;
  transition: .2s;
  line-height: 46px;
  color: rgb(0, 0, 0);
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  text-align: center;
}

.settingsIcon {
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 40px;
  height: 40px;
}

.settingsIcon a {
  display: block;
  width: inherit;
  height: inherit;
  font-size: 20px;
  transition: .2s;
  line-height: 46px;
  color: rgb(0, 0, 0);
  border-radius: 50%;
  text-align: center;
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.1);
}

.right-click-icon a:hover {
  color: rgb(151, 0, 0);
  background: rgba(255, 189, 189, 0.1);
  box-shadow: 0 0 0 2px rgba(151, 1, 1, 0.7);
  // font-size: 24px;
}

.right-click-icon:nth-child(1) a {
  transform: rotate(0deg);
  -ms-transform:rotate(0deg); /* IE 9 */
  -webkit-transform:rotate(0deg); /* Safari and Chrome */
}
.right-click-icon:nth-child(2) a {
  transform: rotate(-60deg);
  -ms-transform:rotate(-60deg);
  -webkit-transform:rotate(-60deg);
}
.right-click-icon:nth-child(3) a {
  transform: rotate(-120deg);
  -ms-transform:rotate(-120deg);
  -webkit-transform:rotate(-120deg);
}
.right-click-icon:nth-child(4) a {
  transform: rotate(-180deg);
  -ms-transform:rotate(-180deg);
  -webkit-transform:rotate(-180deg);
}
.right-click-icon:nth-child(5) a {
  transform: rotate(-240deg);
  -ms-transform:rotate(-240deg);
  -webkit-transform:rotate(-240deg);
}
.right-click-icon:nth-child(6) a {
  transform: rotate(-300deg);
  -ms-transform:rotate(-300deg);
  -webkit-transform:rotate(-300deg);
}
.right-click-icon:nth-child(6) a {
  transform: rotate(-300deg);
  -ms-transform:rotate(-300deg);
  -webkit-transform:rotate(-300deg);
}

.logoStyle {
  background-color: "white";
  width: 70px;
  height: 70px;
}

