@import '../../styles/colors';

.sink-list-menu {
  background-color: white;
  width: max-content;
  padding: 5px;
  box-sizing: border-box;
  text-align: center;
  box-shadow: 1px 1px 7px 1px $colorBoxShadow;
  position: absolute;
  display: table-caption;
  clear: both;
  float: none;
  z-index: 1;
  overflow: auto;
  font-size: 0.85em;
  border-radius: 10px;
  top: 150%;
  right: 150%;
}

.hr-sinkList {
  border: 0;
  height: 2px;
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.9));
}

.items-list {
  cursor: pointer;
}
