@import '../../styles/colors';

.history-pane {
  background-color: $colorWhite;
  box-sizing: border-box;
  box-shadow: 2px 2px 14px 2px $colorBoxShadow;
  font-size: 0.85em;
  position: fixed;
  width: 20%;
  height: 70%;
  border-radius: 10px;
  bottom: 3%;
  right: 1%;
  z-index: 1; 
}

.success-pane {
  position: relative;
  height: 25%;
  width: 90%;
  margin: 1% 3% 0% 3%;
  padding: 2%;
  z-index: 2;
  border: 1px groove $colorNotificationBorder;
  border-radius: 10px;
  box-shadow: 1px 1px 7px 1px $colorBoxShadow;
  overflow: auto;
  background-color: $colorNotificationSuccessBackground;
  background-image: linear-gradient(315deg, $colorNotificationSuccessGradient1 0%, $colorNotificationSuccessGradient2 74%);
}

.warning-pane {
  position: relative;
  height: 25%;
  width: 90%;
  margin: 4% 3% 0% 3%;
  padding: 2%;
  z-index: 2;
  border: 1px groove $colorNotificationBorder;
  border-radius: 10px;
  box-shadow: 1px 1px 7px 1px $colorBoxShadow;
  overflow: auto;
  background-color: $colorNotificationWarningBackground;
  background-image: linear-gradient(315deg, $colorNotificationWarningGradient1 20%, $colorNotificationWarningGradient2 74%);
}

.error-pane {
  position: relative;
  height: 25%;
  width: 90%;
  margin: 4% 3% 4% 3%;
  padding: 2%;
  z-index: 2;
  border: 1px groove $colorNotificationBorder;
  border-radius: 10px;
  box-shadow: 1px 1px 7px 1px $colorBoxShadow;
  overflow: auto;  
  background-color: $colorNotificationErrorBackground;  
  background-image: linear-gradient(147deg, $colorNotificationErrorGradient1 34%, $colorNotificationErrorGradient2 80%);
}

.title-bar {
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  margin-left: 10px;
  margin-bottom: 10px;
}

.closeIcon {
  cursor: pointer;
  position: relative;
  color: $colorBlack;
  width: 4%;
  height: 4%;
  margin: 2% 0% 0% 0%;
  left: 93%;
  z-index: 1;
}

.hr-history-pane  {
  border: 0;
  height: 2px;
  background-image: linear-gradient(to left, $colorWhite, 0.9, $colorWhite, 0.45, $colorWhite 0);
}

.each-toast{
  box-shadow: 2px 2px 12px 3px $colorBoxShadow;
}
