.mainContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 100%;
    height: 100%;
    min-height: 100%;
    background-color: #2F303A;
    display: flex;
    flex-direction: column;
}


.content {
    height: 100%;
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
}

.authContainer {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
}

.titleContainer {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: flex-end;
}


.signupBox{
    width: 70%;
    height: 80%;
    background-color: #202027;
    border-radius: 5px;
}

.formStyle {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.inputContainer {
    width: 100%;
    height: 20%;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
}

.buttonContainer{
    width: 100%;
    height: 20%;
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

.inputStyle {
    color: #fff;
    font-size: 1vw;
    width: 80%;
}

.textInput {
    width: 80%;
    height: 40%;
    border-width: 1px;
    background-color: transparent;
    border-style: solid;
    border-color: #1762A7;
    border-radius: 5px;
    color: #fff;
    font-family: "Poppins";
}

.textInput:focus {
    width: 80%;
    height: 40%;
    border-width: 1px;
    background-color: transparent;
    border-style: solid;
    border-radius: 5px;
    color: #fff;
    font-family: "Poppins";
}

.submitInput {
    width: 82%;
    height: 42%;
    background-color: #176287;
    color: #fff;
    border-radius: 5px;
    border-color: #176287;
    border-style: none;
    border-width: 0px;
    font-size: 1vw;
}

.submitInput:hover {
    cursor: pointer
}

.titleBox {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50%;
    width: 50%;
}

.title {
    font-size: 5vw;
    font-family: "Poppins";
    color: #176287;
    font-weight: bold;

}