.navigationItems {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: 100%;
    flex-direction: row;
    justify-content: space-around;
    width: 60%;
}

.name {
    color: #fff;
    font-family: 'Poppins';
    font-size: 1.3vw;
    font-weight: bold;
}

.logoutItem {
    color: #fff;
    font-family: 'Poppins';
    font-size: 1.3vw;
    font-weight: bold;
    cursor: pointer;
}

.logoutItem:hover {
    color: #40a4cb;
} 