@import '../../styles/colors';

/*
 remove it
*/
body {
    overflow-x: hidden;
    overflow-y: scroll;
}

.App {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 100%;
    height: 100%;
    min-height: 100%;
}

.canvas{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
}


.container{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
}


/*width */ 
 ::-webkit-scrollbar {
     width: 5px;
 }

/* Track */
::-webkit-scrollbar-track {
     box-shadow: inset 0 0 5px $colorScrollBarTrack; 
     border-radius: 5px;
 }

/* Handle */
::-webkit-scrollbar-thumb {
     background: $colorScrollBarThumb; 
     border-radius: 5px;
 }
