@import '../../styles/colors';

.list-container {
    min-height: calc(100% - 100px) !important;
    max-height: calc(100% - 65px);
    overflow-y: scroll !important;

    table:first-child {
      tr {
        td:first-child{
          background-color: rgb(255, 255, 255);
          position: sticky;
          left: 0;
        }
      }
    }
}

.alternative-image {
  position: sticky;
  left: 0;
}