@import '../../styles/colors';

.infobar {
    bottom: 0;
    left: 0;
    margin: 10px;
    margin-left: 40px;
    position: fixed;
  }

.infopar {
    &-a {
      box-sizing: border-box;
      padding: 4px;
      margin: 0 2px 0 2px;
      font-size: smaller;
      display: inline;
      border-radius: 6px;
      background-color: $colorUnselected // non-transparent
    }
  
    &-s {
      box-sizing: border-box;
      padding: 4px;
      margin: 0 2px 0 2px;
      font-size: smaller;
      display: inline;
      border-radius: 6px;
      background-color: $colorSelected;
    }
  
    &-r {
      box-sizing: border-box;
      padding: 4px;
      margin: 0 2px 0 2px;
      font-size: smaller;
      display: none;
      border-radius: 6px;
      background-color: $colorSelected;
    }
  }