@import '../../../styles/_colors';


.collection {
  box-shadow: 2px 2px 14px 2px $colorBoxShadow;
  padding: 14px;
  padding-bottom: 68px;
  box-sizing: border-box;
  height: 100%;
  cursor: pointer;

  &-selected {
    box-shadow: 2px 2px 14px 2px $colorBoxShadow;
    padding: 14px;
    padding-bottom: 68px;
    box-sizing: border-box;
    height: 100%;
    border: 2px;
    border-style: solid;
    border-color: $colorSelected;
    border-radius: 5px;
    box-shadow: 0px 0px 5px $colorSelected; 
    -moz-box-shadow: 0px 0px 5px $colorSelected; 
    -webkit-box-shadow: 0px 0px 5px $colorSelected;  
  }

  &-related {
    box-shadow: 2px 2px 14px 2px $colorBoxShadow;
    padding: 14px;
    padding-bottom: 68px;
    box-sizing: border-box;
    height: 100%;
    border: 2px;
    border-style: solid;
    border-color: $colorSelectedRelated;
    border-radius: 5px;
    box-shadow: 0px 0px 5px $colorSelectedRelated;  
    -moz-box-shadow: 0px 0px 5px $colorSelectedRelated; 
    -webkit-box-shadow: 0px 0px 5px $colorSelectedRelated; 
  }
}

.collection-name-div {
  display: flex;
  flex-direction: row;
  width: 300px;
  border-radius: 12px;
  border: 1px solid #dfdfdf;
  height: 29px;
  align-items: center;
  margin-bottom: 5px;
  -moz-box-shadow: 1px 1px 1px rgba(228, 228, 228, 0.5);
  -webkit-box-shadow: 1px 1px 1px rgba(228, 228, 228, 0.5);
  box-shadow: 1px 1px 1px rgba(228, 228, 228, 0.5);
}

.collection-title-div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 230px;
  height: 29px;
}

.collection-input-div {
  width: 230px;
  height: 29px;
}

.textInputStyle {
  width: 221px;
  height: 23px;
  background-color: #c4c5c5;
}

.collection-titleView-div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 29px;
  border-radius: 12px;
  -moz-box-shadow: 1px 1px 1px rgba(206, 206, 206, 0.5);
  -webkit-box-shadow: 1px 1px 1px rgba(206, 206, 206, 0.5);
  box-shadow: 1px 1px 1px rgba(206, 206, 206, 0.5);
  border-left: 1px solid #dfdfdf;
}

.collection-name {
  display: flex;
  font-size: 16px;
  margin-left: 2px;
}

.collection-name-view {
  display: flex;
  font-size: 16px;
  margin-left: 2px;
}

.tagEdit {
  bottom: 0;
  left: 0;
  margin: 10px;
  position: fixed;
  border-radius: 20px;
  cursor: pointer;
}

.tagEdit:hover {
  background-color: $colorCurrentCommand;
}

.arrow {
  border: solid $colorBlack;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  margin: 3px;
  cursor: pointer;
}

.down {
  transform: rotate(45deg);
}

.up {
  transform: rotate(-135deg);
}

.toggle-button {
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
  border-bottom: 1px solid $colorBoxShadow;
  min-height: 30px;
  box-sizing: border-box
}

.icon-handle {
  display: flex;
  justify-content: space-between;
  min-height: 30px;
}

.pointer {
  border-radius: 20px;
  cursor: pointer;
}

.pointer:hover {
  background-color: $colorCurrentCommand;
}

.pointer2 {
  border-radius: 20px;
  cursor: pointer;
}

.start-drag {
  border-radius: 20px;
  cursor: grab;
}

.start-drag:hover {
  background-color: $colorCurrentCommand;
}

.dragging {
  cursor: grabbing;
}

.multiValueLabel {
  font-family: "Poppins";
  font-size: bold;
}

.arrowIconsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 2px;
}

.arrowIconStyle {
  color: #000;
  display: flex;
  align-items: center;
}

