.contextMenuContainer  {
    width: 100px;
    height: 200px; 
    background-color: #333;
    position: absolute;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.openContainer {
    height: 30px;
    width: 100px;
    margin-top: 3px;
    display: flex;
    align-items: center; 
    cursor: pointer;
}

.openContainer:hover {
    background-color: #18a0fb;
}

.editContainer {
    height: 30px;
