

.panelContent {
    width: 400px;
    //background-color: green;
}

.panelContent ul {
    list-style-type: circle;
    // background-color: grey;
    margin: 0;
    padding: 0;
    // display: flex;
    // align-items: center;
    // flex-direction: column;
}

.panelContent ul li {
    // list-style-type: circle;
    width: 200px;
    height: 35px;
    max-width: 200px;
    // background-color: aqua;
    margin: 2px;
    display: flex;
}

.userItem {
    flex-direction: row;

}

.circle {
    width:30px;
    height: 30px;
    background-color:  #176287;
     margin-right: 5px;
    border-radius: 50%;
    // margin-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #000;
}

.initials {
    font-size: 20px;
    color: white;
    font-weight: bold;
}

.projectTitle {
    font-weight: bold;
    font-family: "Poppins";
    color: #176287;
    margin-top: 5px;
    font-size: 20px;
}

.projectName { 
    display: flex;
    align-items: center;
    font-weight: bold;
    font-family: "Poppins";
    color: #176287;
    font-size: 20px;
}


.homeLink {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 18px;
    border-bottom:2px solid;
    border-color: transparent;
}

.homeLink:hover {
     color: #40a4cb;
    border-bottom:2px solid;
    border-color: #40a4cb;
    cursor: pointer;
}

.projectLink {
    display: flex;
    align-items: center;
    font-weight: bold;
    border-bottom:2px solid;
    border-color: transparent;
    font-size: 18px;
}

.projectLink:hover {
    color: #40a4cb;
    border-bottom:2px solid;
    border-color: #40a4cb;
    cursor: pointer;

}
