@import '../../styles/colors';

html, body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  overflow: hide;
}

.gridLayout{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-bottom: 16px;
  min-height: 100%;
  min-width: 100%;
  overflow-y: scroll;
  overflow-x: scroll;
}

.container  {
  position: inherit;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}