.projectContainer {
    // width: 400px;
    // height: 275px;
    width: 30%;
    margin: 10px;
    max-width: 513px;
    min-width: 383px;
    min-height: 200px;
    margin-bottom: 30px;
    border: 2px solid gray;
    border-radius: 10px;
    display: flex;
    flex-direction: column;

}

.projectContainer:hover {
    border: 3px solid  #00b812;
    cursor: pointer; 
}


.imageContainer {
    display: flex;
    // width: 400px;
    width: 100%;
    height: 200px;
    margin-top: 15px;
    justify-content: center;
}


.textContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 75px;
}


.titleContainer {
    display: flex;
    flex: 1 1;
    border-top: 2px solid gray;
    border-radius: 4px;
    margin: 0;
    justify-content: space-between;
}
.title {
    margin: 0;
    margin-left: 4px;
    font-weight: bold;
    font-size: 20px;
    font-family: "Poppins";
    margin-left: 15px;
    margin-top: 5px;
    color: #176287;
    width: 340px;
    height: 32px;
}

.numberOfAlts {
    height: 26px;
    display: flex;
    width: 32px;
    background-color: green;
    align-self: center;
    justify-content: center;
    margin-left: 8px;
    border-radius: 5px;
    align-items: center;
    margin-right: 3px;
}

.imageStyle {
    width: 40%;
    height: 90%;
}

.descriptionContainer{
    display: flex;
    flex: 1;
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-between;
}

.description {
    margin: 0;
    margin-left: 5px;
    font-size: 13px;
    font-family: "Poppins";
    border-radius: 2px;
    margin-left: 15px;
}

.userContainer {
    width: 67px;
    height: 37px;
    // background-color: blue;
    display: flex;
    
}


.circle {
    width:30px;
    height: 30px;
    background-color:  #176287;
    //margin-right: 5px;
    border-radius: 50%;
    // margin-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #000;
    margin-right: 0.5px;
}

.icon-style {
    width:30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #000;
    margin-right: 2px;
}
.icon-style:hover {
    border: 2px solid #176287;
    color: #176287;
}

.initials {
    font-size: 20px;
    color: white;
    font-weight: bold;
}







